
<script>
/* ============
   * Minimal Layout
   * ============
   *
   * Used for the register and login pages.
   *
   * Layouts are used to store a lot of shared code.
   * This way the app stays clean.
   */
import Vue from 'vue';
import { mapGetters } from 'vuex';

export default {
	data: () => ({
		offsetTop: 0,
		primaryLinks: [
			{ path: { name: 'home.index' }, text: 'Home', permissions: [] },
			{ path: { name: 'login.index' }, text: 'About Us', permissions: [] },
			{ path: { name: 'login.index' }, text: 'Services', permissions: [] },
			{ path: { name: 'login.index' }, text: 'Prices', permissions: [] },
			{ path: { name: 'login.index' }, text: 'Guarantees', permissions: [] },
			{ path: { name: 'login.index' }, text: 'Contact Us', permissions: [] },
		],
		secondaryLinks: [
			{ path: { name: 'login.index' }, icon: 'phone', text: '+012 256 25 34', permissions: [] },
			{ path: { name: 'login.index' }, icon: 'person', text: 'Login', permissions: [] },
		],
		footerIcons: ['fa-facebook', 'fa-twitter', 'fa-google-plus', 'fa-linkedin', 'fa-instagram'],
		isFooterFixed: false,
		secondaryFooterRows: [
			{
				img: 'network-contact.png',
				title: 'Our Services',
				children: ['MDBootstrap', 'MDWordPress', 'BrandFlow', 'Bootstrap Angular'],
			},
			{
				img: 'network-contact.png',
				title: 'Quick Links',
				children: ['Home', 'Contact Us', 'FAQs', 'Pricing', 'Services', 'Search'],
			},
			{
				img: 'person-contact.svg',
				title: 'My Account',
				children: ['Sign In', 'Sign Up', 'Upgrade Account', 'Become an Affiliate', 'Find Helper'],
			},
		],
	}),
	computed: {
		...mapGetters({}),
	},
	watch: {},
	methods: {
		onScroll() {
			this.offsetTop = window.pageYOffset || document.documentElement.scrollTop;
			console.log('The current offset ', this.offsetTop);
		},
		elOffset(el) {
			const rect = el.getBoundingClientRect();
			const scrollLeft = window.pageXOffset || document.documentElement.scrollLeft;
			const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
			return { top: rect.top + scrollTop, left: rect.left + scrollLeft };
		},
		toTop() {
			Vue.nextTick(() => {
				this.$vuetify.goTo(10, {
					duration: 500,
					offset: -100,
					easing: 'easeInOutCubic',
				});
			});
		},
	},
	mounted() {
		this.isFooterFixed = this.elOffset(this.$refs.fixedFooter).top <= window.innerHeight;
	},
};
</script>
<template>
  <v-app id="premium-home" class="premium-home" standalone v-scroll="onScroll">
    <v-container fluid pa-0>
      <v-layout row wrap class="p-relative">
        <v-toolbar class="elevation-0 transparent logo-toolbar px-5">
          <v-toolbar-title>
            <img src="/static/img/home/basic-logo.png" alt="Premium Essay Writers"/>
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <router-link
            v-for="(l, i) in secondaryLinks"
            :key="i"
            :to="l.path"
            :exact="false"
            active-class="link-active"
          >
            <span class="secondary-links">
              <v-icon small>{{ l.icon }}</v-icon>
              {{ l.text }}
            </span>
          </router-link>
        </v-toolbar>
        <v-toolbar 
          class="elevation-0 transparent menu-toolbar px-5"
          :class="{'fixed-toolbar': offsetTop > 80}"
        >
          <router-link
            v-for="(l, i) in primaryLinks"
            :key="i"
            :to="l.path"
            :exact="false"
            active-class="link-active"
          >
            <v-btn flat class="ma-0"> {{ l.text }} </v-btn>
          </router-link>
          <v-spacer></v-spacer>
          <v-text-field prepend-icon="search" hide-details single-line placeholder="Search"></v-text-field>
          <v-btn icon class="mr-0">
            <v-icon>more_vert</v-icon>
          </v-btn>
        </v-toolbar>
        <img 
          src="/static/img/home/basic-logo.png" 
          alt="Premium Essay Writers" 
          class="fixed-logo px-5" 
          :class="{'fixed-logo-fixed': offsetTop > 80}"
        />
        
        <!-- The slot to come in right after the toolbar app frame -->
        <slot name="get-frame"></slot>


        <!-- The Back to top Button -->
        <v-btn
          bottom
          fixed
          floating
          left
          dark
          fab
          color="app-c-red darken-4"
          class="go-top elevation-20"
          @click="toTop"
        >
          <v-icon size="32px">keyboard_arrow_up</v-icon>
        </v-btn>

        <!-- App Footer Slot -->
        <slot name="app-footer">
          <v-footer height="auto" class="full-width">
            <v-card flat tile class="full-width" >
              
              <!-- Secondary Footer Slot -->
              <slot name="secondary-footer">

                <!-- Include Secondary Footer Slot -->
                <v-card-text class="secondary-footer app-c-grey-dark--text px-5 text-xs-center text-sm-left">
                  <v-container fluid>
                    <v-layout row wrap>
                      <v-flex xs12 sm6 md3 layout column class="b-3">
                        <div class="footer-item-container">
                          <div  class="footer-title-img text-xs-left py-3 px-2">
                            <img src="/static/img/home/phone-contact.png" alt="avatar">
                          </div>
                          <span class="footer-title py-1">TALK TO US</span>
                          <div class="footer-item-data">
                            <v-icon size="18px" class="mr-3" color="app-c-grey-dark">fa-home</v-icon>
                            New York, NY 10012, US
                          </div>
                          <div class="footer-item-data">
                            <v-icon size="18px" class="mr-3" color="app-c-grey-dark">fa-envelope</v-icon>
                            info@example.com
                          </div>
                          <div class="footer-item-data">
                            <v-icon size="18px" class="mr-3" color="app-c-grey-dark">fa-phone</v-icon>
                            + 01 234 567 88
                          </div>
                          <div class="footer-item-data">
                            <v-icon size="18px" class="mr-3" color="app-c-grey-dark">fa-print</v-icon>
                            + 01 234 567 89
                          </div>
                        </div>
                      </v-flex>
                      <v-flex
                        v-for="(col, i) in secondaryFooterRows"
                        :key="i"
                        xs12 sm6 md3
                        class="pb-3"
                      >
                        <div class="footer-item-container">
                          <div  class="footer-title-img py-3 px-2">
                            <img :src="'/static/img/home/' + col.img" alt="avatar">
                          </div>
                          <span class="footer-title py-1" v-text="col.title.toUpperCase()"></span>
                          <div
                            v-for="(child, i) in col.children"
                            :key="i"
                            v-text="child"
                            class="footer-item-data app-link-grey"
                          ></div>
                        </div>
                      </v-flex>
                    </v-layout>
                  </v-container>
                  <v-divider class="app-c-grey-dark my-2"></v-divider>
                  <v-layout>
                    <v-flex xs12 class="text-xs-center py-1">
                      <span class="footer-link app-link-grey">Privacy</span>
                      <span class="footer-link app-link-grey">Terms of Use</span>
                      <span class="footer-link app-link-grey">FAQs</span>
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </slot>

              <v-card-text 
                ref="fixedFooter" 
                class="social text-xs-center pt-2 pb-4 px-5"
                :class="{'fixed-footer': isFooterFixed}"
              >
                <v-btn
                  v-for="icon in footerIcons"
                  :key="icon"
                  icon
                  large
                  class="mx-3 white--text"
                >
                  <v-icon size="20px">{{ icon }}</v-icon>
                </v-btn>
                <br />
                <div class="copyright justify-center pt-1">
                  Copyright 2018 <strong>PremiumEssayWriters .</strong> All rights reserved.
                </div>
              </v-card-text>
            </v-card>
          </v-footer>
        </slot>

      </v-layout>
    </v-container>
  </v-app>
</template>

<style lang="stylus">
.premium-home
  /* FF3.6-15 */
  background -moz-linear-gradient(top, rgba(196,138,250,0.8) 0%, rgba(111,146,190,0.73) 43%, rgba(111,146,190,0.7) 58%, rgba(100,151,186,0.69) 65%, rgba(116,166,220,0.67) 75%, rgba(105,169,213,0.65) 86%, rgba(98,171,211,0.63) 99%, rgba(98,171,211,0.63) 100%) !important
  /* Chrome10-25,Safari5.1-6 */
  background -webkit-linear-gradient(top, rgba(196,138,250,0.8) 0%,rgba(111,146,190,0.73) 43%,rgba(111,146,190,0.7) 58%,rgba(100,151,186,0.69) 65%,rgba(116,166,220,0.67) 75%,rgba(105,169,213,0.65) 86%,rgba(98,171,211,0.63) 99%,rgba(98,171,211,0.63) 100%) !important
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background linear-gradient(to bottom, rgba(196,138,250,0.8) 0%,rgba(111,146,190,0.73) 43%,rgba(111,146,190,0.7) 58%,rgba(100,151,186,0.69) 65%,rgba(116,166,220,0.67) 75%,rgba(105,169,213,0.65) 86%,rgba(98,171,211,0.63) 99%,rgba(98,171,211,0.63) 100%) !important
  /* IE6-9*/
  // filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ccc48afa', endColorstr='#a162abd3',GradientType=0) !important
  .home-section
    line-height normal
  .toolbar
    .toolbar__content
      min-height 8vh
  .menu-toolbar
    transition all 0.5s ease-in-out
    .toolbar__content
      margin-left -32px !important
      @media screen and (min-width: 1059px)
        margin-top -16px !important
      button
        position relative
        color #13242a
        background transparent
        line-height 1.2
        font-size 16px
        text-transform none
        .btn__content
          padding 0 8px
  .logo-toolbar
    .toolbar__title
      font-size 0px
      img
        width 200px
    .toolbar__content
      height 64px !important
      margin-left -16px !important
    .secondary-links
      font-size 13px
      font-weight 600
      margin-left 16px
      color #17272f
  .fixed-toolbar
    position fixed
    z-index 100
    max-width 100%
    background-color #95b7d3 !important
    padding 8px 0
    .toolbar__content
      padding 0
      margin-top 0 !important
  .fixed-logo
    display none
  .fixed-logo-fixed
    display block
    position fixed
    z-index 90
    padding-top 84px

  footer
    background-color #98b9d8 !important
    .card
      .social
        background-color #000000 !important
        color #e0e0e0 !important
        transition all 0.5s ease-in-out
        button
          border 1px solid
        .copyright
          font-size 11px
      .fixed-footer
        position fixed
        bottom 0
  .go-top
    visibility hidden
    transition all 1.5s cubic-bezier(.25,.8,.5,1) !important
    transform-origin center center 0px
    border 2px solid #95b7d4
  .secondary-footer
    background-color #161616 !important
    .footer-item-container
      max-width 230px
      margin 0 auto
      .footer-title
        color #76abdc
        font-size 14px
        font-weight 700
        font-family Lato, Montserrat, open-sans
        line-height 2
      .footer-item-data
        font-style normal
        font-weight 500
        font-size 13px
        padding 2px 0
      .footer-title-img
        img
          width 40%
          max-height 120px
          margin 0 auto
    .footer-link
      font-size 11px
      padding 5px 3% !important
        
</style>




